import React, { useState } from "react";
import {
  Container,
  Header,
  Table,
  Segment,
  List,
  Divider,
  Card,
  Image,
  Grid,
  Icon,
  Button,
  Label,
  Tab,
  Rating,
  Progress
} from "semantic-ui-react";
import { PageLayout } from "../../layout/PageLayout/PageLayout";

const frameworks = [
  {
    name: "Bootstrap",
    popularity: 5,
    easeOfUse: 4,
    customization: 3,
    performance: 3,
    ecosystem: 4,
    image: "https://themewagon.com/wp-content/uploads/2018/08/Feat-Free-Bootstrap-4-UI-Kits-1.jpg",
    color: "blue",
    description: "Универсальный фреймворк с богатой экосистемой и готовыми компонентами. Идеален для быстрой разработки.",
    bestFor: ["Быстрая разработка", "Начинающие разработчики", "Универсальные проекты"],
    icon: "bootstrap",
    pros: ["Огромное сообщество", "Множество готовых шаблонов", "Отличная документация"],
    cons: ["Сайты выглядят похожими", "Большой размер файлов", "Ограниченная кастомизация"]
  },
  {
    name: "Tailwind CSS",
    popularity: 4,
    easeOfUse: 3,
    customization: 5,
    performance: 4,
    ecosystem: 4,
    image: "https://i2.wp.com/www.tailwindawesome.com/assets/social_share-d2806c27be686e78ca774962aece7a148b6c466e6b6b2e863aec94d8bf237dfe.png",
    color: "teal",
    description: "Утилитарный CSS-фреймворк с полной свободой кастомизации. Отлично подходит для уникальных дизайнов.",
    bestFor: ["Уникальные дизайны", "Опытные разработчики", "Кастомные проекты"],
    icon: "code",
    pros: ["Максимальная гибкость", "Малый размер итогового CSS", "Нет нежелательных стилей"],
    cons: ["Крутая кривая обучения", "Длинные классы в HTML", "Требует дополнительной настройки"]
  },
  {
    name: "Carbon",
    popularity: 3,
    easeOfUse: 3,
    customization: 3,
    performance: 4,
    ecosystem: 3,
    image: "https://i.pinimg.com/736x/fc/18/ce/fc18cef61b1dbd528e099be1e63cb372.jpg",
    color: "black",
    description: "Профессиональный фреймворк от IBM для создания корпоративных приложений с высоким уровнем доступности.",
    bestFor: ["Корпоративные приложения", "Бизнес-решения", "Профессиональные интерфейсы"],
    icon: "briefcase",
    pros: ["Профессиональный вид", "Доступность на высоком уровне", "Единый стиль компонентов"],
    cons: ["Ограниченная гибкость стилей", "Меньшее сообщество", "Сложность интеграции"]
  },
  {
    name: "Semantic UI",
    popularity: 3,
    easeOfUse: 4,
    customization: 3,
    performance: 3,
    ecosystem: 3,
    image: "https://cdn.cssauthor.com/wp-content/uploads/2023/02/Semantic-UI.jpg?strip=all&lossy=1&resize=1000%2C600&ssl=1",
    color: "violet",
    description: "Фреймворк с интуитивно понятным API и семантическими классами. Быстрый старт и понятная структура.",
    bestFor: ["Средние проекты", "Семантическая верстка", "Прототипирование"],
    icon: "paint brush",
    pros: ["Понятный синтаксис", "Готовые темы", "Простота использования"],
    cons: ["Меньшая популярность", "Размер файлов", "Не всегда актуальные обновления"]
  },
];

const ComparisonPage = () => {
  const [activeFramework, setActiveFramework] = useState(frameworks[0]);

  const panes = [
    { menuItem: 'Таблица сравнения', render: () => <Tab.Pane attached={false}>{renderComparisonTable()}</Tab.Pane> },
    { menuItem: 'Карточки фреймворков', render: () => <Tab.Pane attached={false}>{renderFrameworkCards()}</Tab.Pane> },
    { menuItem: 'Детальный анализ', render: () => <Tab.Pane attached={false}>{renderDetailedAnalysis()}</Tab.Pane> },
  ];

  function renderComparisonTable() {
    return (
      <Segment raised padded className="comparison-table-segment">
        <Table celled structured className="comparison-table">
          <Table.Header>
            <Table.Row>
              <Table.HeaderCell style={{ width: '140px' }}>Фреймворк</Table.HeaderCell>
              <Table.HeaderCell>Популярность</Table.HeaderCell>
              <Table.HeaderCell>Удобство</Table.HeaderCell>
              <Table.HeaderCell>Гибкость</Table.HeaderCell>
              <Table.HeaderCell>Производительность</Table.HeaderCell>
              <Table.HeaderCell>Экосистема</Table.HeaderCell>
              <Table.HeaderCell>Лучше всего для</Table.HeaderCell>
            </Table.Row>
          </Table.Header>
          <Table.Body>
            {frameworks.map((fw, index) => (
              <Table.Row key={index} active={activeFramework.name === fw.name} 
                         onClick={() => setActiveFramework(fw)}
                         style={{ cursor: 'pointer' }}>
                <Table.Cell>
                  <Header as="h4">
                    <Icon name={fw.icon} color={fw.color} />
                    <Header.Content>{fw.name}</Header.Content>
                  </Header>
                </Table.Cell>
                <Table.Cell><Rating icon='star' defaultRating={fw.popularity} maxRating={5} disabled /></Table.Cell>
                <Table.Cell><Rating icon='star' defaultRating={fw.easeOfUse} maxRating={5} disabled /></Table.Cell>
                <Table.Cell><Rating icon='star' defaultRating={fw.customization} maxRating={5} disabled /></Table.Cell>
                <Table.Cell><Rating icon='star' defaultRating={fw.performance} maxRating={5} disabled /></Table.Cell>
                <Table.Cell><Rating icon='star' defaultRating={fw.ecosystem} maxRating={5} disabled /></Table.Cell>
                <Table.Cell>
                  {fw.bestFor.map((item, i) => (
                    <Label key={i} color={fw.color} size="tiny" style={{ margin: '2px' }}>
                      {item}
                    </Label>
                  ))}
                </Table.Cell>
              </Table.Row>
            ))}
          </Table.Body>
        </Table>
      </Segment>
    );
  }

  function renderFrameworkCards() {
    return (
      <Grid columns={2} stackable doubling>
        {frameworks.map((fw, index) => (
          <Grid.Column key={index}>
            <Card fluid className="framework-comparison-card" onClick={() => setActiveFramework(fw)}>
              <div style={{ position: 'relative', height: '200px', overflow: 'hidden' }}>
                <Image 
                  src={fw.image} 
                  style={{ 
                    objectFit: 'cover', 
                    height: '100%', 
                    width: '100%',
                    filter: 'brightness(0.85)' 
                  }}
                />
                <Label corner="left" size="big" color={fw.color} icon={fw.icon} />
                <div style={{ 
                  position: 'absolute', 
                  bottom: '0', 
                  left: '0', 
                  right: '0',
                  padding: '15px',
                  background: 'linear-gradient(to top, rgba(0,0,0,0.8), transparent)',
                  color: 'white'
                }}>
                  <Header as="h3" inverted style={{ margin: '0' }}>{fw.name}</Header>
                </div>
              </div>
              <Card.Content>
                <Card.Description>
                  {fw.description}
                </Card.Description>
                <Divider />
                <Grid columns={2} style={{ margin: '-0.5rem' }}>
                  <Grid.Column>
                    <div><strong>Популярность:</strong></div>
                    <Progress percent={fw.popularity * 20} size="tiny" color={fw.color} style={{ margin: '5px 0' }} />
                  </Grid.Column>
                  <Grid.Column>
                    <div><strong>Гибкость:</strong></div>
                    <Progress percent={fw.customization * 20} size="tiny" color={fw.color} style={{ margin: '5px 0' }} />
                  </Grid.Column>
                </Grid>
              </Card.Content>
              <Card.Content extra>
                <Button 
                  basic 
                  color={fw.color} 
                  fluid
                  content='Подробнее'
                  icon='arrow right'
                  labelPosition='right'
                />
              </Card.Content>
            </Card>
          </Grid.Column>
        ))}
      </Grid>
    );
  }

  function renderDetailedAnalysis() {
    return (
      <div>
        <Segment raised padded className="selected-framework-info" color={activeFramework.color}>
          <Grid columns={2} stackable>
            <Grid.Column width={5}>
              <Image src={activeFramework.image} rounded bordered />
              <Header as="h2" style={{ marginTop: '15px' }}>
                <Icon name={activeFramework.icon} color={activeFramework.color} />
                <Header.Content>
                  {activeFramework.name}
                  <Header.Subheader>
                    Общая оценка: {((activeFramework.popularity + activeFramework.easeOfUse + 
                                    activeFramework.customization + activeFramework.performance + 
                                    activeFramework.ecosystem) / 5).toFixed(1)} / 5
                  </Header.Subheader>
                </Header.Content>
              </Header>
              <div style={{ marginTop: '20px' }}>
                <Header as="h4">Лучше всего подходит для:</Header>
                <List>
                  {activeFramework.bestFor.map((item, i) => (
                    <List.Item key={i}>
                      <Icon name="check" color={activeFramework.color} />
                      <List.Content>{item}</List.Content>
                    </List.Item>
                  ))}
                </List>
              </div>
            </Grid.Column>
            <Grid.Column width={11}>
              <p style={{ fontSize: '1.1em', lineHeight: '1.6' }}>{activeFramework.description}</p>
              
              <Grid columns={2} stackable style={{ marginTop: '20px' }}>
                <Grid.Column>
                  <Segment color="green">
                    <Header as="h4" color="green">
                      <Icon name="plus" /> Преимущества
                    </Header>
                    <List>
                      {activeFramework.pros.map((item, i) => (
                        <List.Item key={i}>
                          <Icon name="check circle" color="green" />
                          <List.Content>{item}</List.Content>
                        </List.Item>
                      ))}
                    </List>
                  </Segment>
                </Grid.Column>
                <Grid.Column>
                  <Segment color="red">
                    <Header as="h4" color="red">
                      <Icon name="minus" /> Недостатки
                    </Header>
                    <List>
                      {activeFramework.cons.map((item, i) => (
                        <List.Item key={i}>
                          <Icon name="exclamation circle" color="red" />
                          <List.Content>{item}</List.Content>
                        </List.Item>
                      ))}
                    </List>
                  </Segment>
                </Grid.Column>
              </Grid>
              
              <Segment secondary style={{ marginTop: '20px' }}>
                <Header as="h4">Детальные оценки</Header>
                <Grid columns={2} stackable>
                  <Grid.Column>
                    <div><strong>Популярность:</strong></div>
                    <Progress percent={activeFramework.popularity * 20} size="small" color={activeFramework.color} style={{ margin: '5px 0' }} />
                    <div><strong>Удобство использования:</strong></div>
                    <Progress percent={activeFramework.easeOfUse * 20} size="small" color={activeFramework.color} style={{ margin: '5px 0' }} />
                    <div><strong>Гибкость:</strong></div>
                    <Progress percent={activeFramework.customization * 20} size="small" color={activeFramework.color} style={{ margin: '5px 0' }} />
                  </Grid.Column>
                  <Grid.Column>
                    <div><strong>Производительность:</strong></div>
                    <Progress percent={activeFramework.performance * 20} size="small" color={activeFramework.color} style={{ margin: '5px 0' }} />
                    <div><strong>Экосистема:</strong></div>
                    <Progress percent={activeFramework.ecosystem * 20} size="small" color={activeFramework.color} style={{ margin: '5px 0' }} />
                    <div><strong>Общая оценка:</strong></div>
                    <Progress 
                      percent={((activeFramework.popularity + activeFramework.easeOfUse + 
                                activeFramework.customization + activeFramework.performance + 
                                activeFramework.ecosystem) / 5) * 20} 
                      size="small" 
                      color={activeFramework.color} 
                      style={{ margin: '5px 0' }} 
                    />
                  </Grid.Column>
                </Grid>
              </Segment>
            </Grid.Column>
          </Grid>
        </Segment>
      </div>
    );
  }

  return (
    <PageLayout>
      {/* Hero Section */}
      <div style={{
        background: 'linear-gradient(135deg, #4776E6 0%, #8E54E9 100%)',
        padding: "60px 0",
        color: 'white',
        textAlign: 'center',
        marginBottom: '30px',
        borderRadius: '0 0 10px 10px'
      }}>
        <Container>
          <Header as="h1" style={{ fontSize: '2.5em', marginBottom: '10px' }}>
            Сравнение CSS-фреймворков
            <Header.Subheader style={{ color: 'rgba(255,255,255,0.9)', fontSize: '0.5em', marginTop: '10px' }}>
              Подробный анализ популярных инструментов для стилизации веб-приложений
            </Header.Subheader>
          </Header>
          <p style={{ fontSize: '1.2em', maxWidth: '700px', margin: '0 auto', marginTop: '20px' }}>
            Выберите оптимальный CSS-фреймворк для вашего проекта на основе детального сравнения 
            популярности, удобства использования, гибкости и производительности.
          </p>
        </Container>
      </div>

      <Container style={{ marginTop: '30px', marginBottom: '50px' }}>
        <Segment basic>
          <Header as="h2" dividing>
            <Icon name="code" />
            <Header.Content>
              Сравнительный анализ CSS-фреймворков
              <Header.Subheader>
                Выберите вкладку для просмотра информации в разных форматах
              </Header.Subheader>
            </Header.Content>
          </Header>
          
          <Tab menu={{ secondary: true, pointing: true }} panes={panes} />
        </Segment>

        <Segment basic style={{ marginTop: '40px' }}>
          <Header as="h3" dividing>
            <Icon name="lightbulb outline" />
            <Header.Content>
              Как выбрать подходящий CSS-фреймворк?
            </Header.Content>
          </Header>
          
          <Grid columns={3} stackable>
            <Grid.Column>
              <Segment raised padded>
                <Label ribbon color="blue">Шаг 1</Label>
                <Header as="h4">Определите требования проекта</Header>
                <p>Оцените масштаб проекта, требования к дизайну и уникальности, а также технические ограничения.</p>
                <List bulleted>
                  <List.Item>Насколько уникальным должен быть дизайн?</List.Item>
                  <List.Item>Какие сроки разработки?</List.Item>
                  <List.Item>Насколько важна производительность?</List.Item>
                </List>
              </Segment>
            </Grid.Column>
            <Grid.Column>
              <Segment raised padded>
                <Label ribbon color="teal">Шаг 2</Label>
                <Header as="h4">Оцените свои навыки</Header>
                <p>Учитывайте опыт команды и время, которое можно потратить на изучение нового инструмента.</p>
                <List bulleted>
                  <List.Item>Знакомы ли разработчики с фреймворком?</List.Item>
                  <List.Item>Есть ли время на обучение?</List.Item>
                  <List.Item>Какой уровень CSS-знаний у команды?</List.Item>
                </List>
              </Segment>
            </Grid.Column>
            <Grid.Column>
              <Segment raised padded>
                <Label ribbon color="violet">Шаг 3</Label>
                <Header as="h4">Сравните и тестируйте</Header>
                <p>Создайте прототипы с разными фреймворками и оцените результаты на практике.</p>
                <List bulleted>
                  <List.Item>Сравните скорость разработки</List.Item>
                  <List.Item>Оцените итоговый размер файлов</List.Item>
                  <List.Item>Проверьте совместимость с другими инструментами</List.Item>
                </List>
              </Segment>
            </Grid.Column>
          </Grid>
        </Segment>

        <Segment basic textAlign="center" style={{ marginTop: '40px' }}>
          <Header as="h3">Не можете определиться?</Header>
          <p>Свяжитесь с нашими экспертами для получения персональной консультации по выбору CSS-фреймворка</p>
          <Button primary size="large">
            <Icon name="mail" /> Получить консультацию
          </Button>
        </Segment>
      </Container>
    </PageLayout>
  );
};

export default ComparisonPage;
