import React, { useEffect, useState } from "react";
import {
  Button,
  Container,
  Divider,
  Form,
  Grid,
  Header,
  Icon,
  Input,
  List,
  Message,
  Segment,
} from "semantic-ui-react";
import { PageLayout } from "../../layout/PageLayout/PageLayout";

const ContactPage = () => {
  const [windowWidth, setWindowWidth] = useState(window.innerWidth);
  const isMobile = windowWidth <= 768;

  // Обработчик изменения размера окна
  useEffect(() => {
    const handleResize = () => setWindowWidth(window.innerWidth);
    window.addEventListener("resize", handleResize);
    return () => window.removeEventListener("resize", handleResize);
  }, []);
  // Состояние формы
  const [formData, setFormData] = useState({
    name: "",
    email: "",
    subject: "",
    message: "",
  });

  // Состояние валидации
  const [errors, setErrors] = useState({});

  // Состояние отправки формы
  const [formStatus, setFormStatus] = useState({
    success: false,
    error: false,
    loading: false,
  });

  // Обработчик изменения полей формы
  const handleChange = (e, { name, value }) => {
    setFormData({ ...formData, [name]: value });

    // Очистка ошибки при вводе
    if (errors[name]) {
      const newErrors = { ...errors };
      delete newErrors[name];
      setErrors(newErrors);
    }
  };

  // Валидация формы
  const validateForm = () => {
    const newErrors = {};

    if (!formData.name) newErrors.name = "Пожалуйста, введите ваше имя";
    if (!formData.email) newErrors.email = "Пожалуйста, введите ваш email";
    else if (!/\S+@\S+\.\S+/.test(formData.email))
      newErrors.email = "Пожалуйста, введите корректный email";
    if (!formData.subject)
      newErrors.subject = "Пожалуйста, укажите тему сообщения";
    if (!formData.message)
      newErrors.message = "Пожалуйста, введите ваше сообщение";

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  // Обработчик отправки формы
  const handleSubmit = () => {
    if (validateForm()) {
      setFormStatus({ ...formStatus, loading: true });

      // Имитация отправки формы с задержкой
      setTimeout(() => {
        setFormStatus({
          loading: false,
          success: true,
          error: false,
        });

        // Сброс формы после успешной отправки
        setFormData({
          name: "",
          email: "",
          subject: "",
          message: "",
        });

        // Сброс сообщения об успехе через 5 секунд
        setTimeout(() => {
          setFormStatus({
            loading: false,
            success: false,
            error: false,
          });
        }, 5000);
      }, 1500);
    }
  };

  // CSS-фреймворки и их ссылки
  const cssFrameworks = [
    {
      name: "Semantic UI",
      docs: "https://semantic-ui.com/",
      react: "https://react.semantic-ui.com/",
      icon: "react",
      color: "#00b5ad",
    },
    {
      name: "Bootstrap",
      docs: "https://getbootstrap.com/",
      react: "https://react-bootstrap.github.io/",
      icon: "bootstrap",
      color: "#7952b3",
    },
    {
      name: "Tailwind CSS",
      docs: "https://tailwindcss.com/",
      react: "https://tailwindcss.com/docs/guides/react",
      icon: "code",
      color: "#38b2ac",
    },
    {
      name: "Carbon Design System",
      docs: "https://carbondesignsystem.com/",
      react: "https://react.carbondesignsystem.com/",
      icon: "ibm",
      color: "#161616",
    },
  ];

  // Социальные сети
  const socialLinks = [
    {
      name: "GitHub",
      icon: "github",
      link: "https://github.com/",
      color: "#333",
    },
    {
      name: "Twitter",
      icon: "twitter",
      link: "https://twitter.com/",
      color: "#1DA1F2",
    },
    {
      name: "LinkedIn",
      icon: "linkedin",
      link: "https://linkedin.com/",
      color: "#0077B5",
    },
    {
      name: "Telegram",
      icon: "telegram",
      link: "https://t.me/",
      color: "#0088cc",
    },
  ];

  return (
    <PageLayout>
      {/* Hero секция */}
      <div
        style={{
          background: "linear-gradient(135deg, #4568dc, #b06ab3)",
          color: "white",
          padding: isMobile ? "2rem 0" : "3rem 0",
          marginBottom: "2rem",
          boxShadow: "0 4px 12px rgba(0,0,0,0.1)",
        }}
      >
        <Container text textAlign="center">
          <Header
            as="h1"
            style={{
              fontSize: isMobile ? "2rem" : "3rem",
              color: "white",
              marginBottom: "1rem",
            }}
          >
            <Icon name="mail" />
            Связаться с нами
          </Header>
          <p
            style={{
              fontSize: isMobile ? "1rem" : "1.2rem",
              lineHeight: "1.6",
              padding: isMobile ? "0 1rem" : 0,
            }}
          >
            Мы всегда рады помочь вам и ответить на любые вопросы. Заполните
            форму ниже или воспользуйтесь другими способами связи.
          </p>
        </Container>
      </div>

      <Container
        style={{ marginBottom: "3em", padding: isMobile ? "0 1rem" : 0 }}
      >
        <Grid stackable columns={2} relaxed="very">
          <Grid.Column width={isMobile ? 16 : 9}>
            <Segment
              raised
              padded={isMobile ? "very" : "very"}
              style={{
                borderRadius: "10px",
                boxShadow: "0 2px 10px rgba(0,0,0,0.08)",
                marginBottom: isMobile ? "1.5rem" : 0,
              }}
            >
              <Header
                as="h2"
                style={{ color: "#4568dc", marginBottom: "1.5rem" }}
              >
                <Icon name="paper plane outline" />
                <Header.Content>
                  Форма обратной связи
                  <Header.Subheader
                    style={{ marginTop: "0.5rem", color: "#666" }}
                  >
                    Отправьте нам сообщение, и мы свяжемся с вами в ближайшее
                    время
                  </Header.Subheader>
                </Header.Content>
              </Header>

              {formStatus.success && (
                <Message
                  positive
                  icon="check circle"
                  header="Сообщение отправлено"
                  content="Спасибо за ваше сообщение! Мы ответим вам в ближайшее время."
                  style={{ borderRadius: "8px" }}
                />
              )}

              {formStatus.error && (
                <Message
                  negative
                  icon="times circle"
                  header="Ошибка отправки"
                  content="Пожалуйста, попробуйте отправить сообщение позже."
                  style={{ borderRadius: "8px" }}
                />
              )}

              <Form
                onSubmit={handleSubmit}
                loading={formStatus.loading}
                size={isMobile ? "medium" : "large"}
              >
                <Form.Group widths="equal" stackable={isMobile}>
                  <Form.Field
                    id="form-input-name"
                    control={Input}
                    label="Имя"
                    name="name"
                    placeholder="Введите ваше имя"
                    value={formData.name}
                    onChange={handleChange}
                    icon="user"
                    iconPosition="left"
                    error={
                      errors.name
                        ? { content: errors.name, pointing: "below" }
                        : null
                    }
                  />
                  <Form.Field
                    id="form-input-email"
                    control={Input}
                    label="Email"
                    name="email"
                    placeholder="example@mail.com"
                    value={formData.email}
                    onChange={handleChange}
                    icon="envelope"
                    iconPosition="left"
                    error={
                      errors.email
                        ? { content: errors.email, pointing: "below" }
                        : null
                    }
                  />
                </Form.Group>
                <Form.Field
                  id="form-input-subject"
                  control={Input}
                  label="Тема"
                  name="subject"
                  placeholder="Тема вашего сообщения"
                  value={formData.subject}
                  onChange={handleChange}
                  icon="tag"
                  iconPosition="left"
                  error={
                    errors.subject
                      ? { content: errors.subject, pointing: "below" }
                      : null
                  }
                />
                <Form.TextArea
                  label="Сообщение"
                  name="message"
                  placeholder="Напишите ваше сообщение..."
                  value={formData.message}
                  onChange={handleChange}
                  style={{ minHeight: isMobile ? "120px" : "150px" }}
                  error={
                    errors.message
                      ? { content: errors.message, pointing: "below" }
                      : null
                  }
                />
                <Button
                  type="submit"
                  size={isMobile ? "medium" : "large"}
                  fluid={isMobile}
                  style={{
                    background: "linear-gradient(135deg, #4568dc, #b06ab3)",
                    color: "white",
                    borderRadius: "30px",
                    padding: isMobile ? "12px 20px" : "15px 30px",
                    marginTop: "1rem",
                  }}
                >
                  <Icon name="send" /> Отправить сообщение
                </Button>
              </Form>
            </Segment>
          </Grid.Column>

          <Grid.Column width={isMobile ? 16 : 7}>
            <Segment
              raised
              padded="very"
              style={{
                borderRadius: "10px",
                boxShadow: "0 2px 10px rgba(0,0,0,0.08)",
              }}
            >
              <Header as="h2" style={{ color: "#4568dc" }}>
                <Icon name="map marker alternate" />
                <Header.Content>
                  Наш адрес
                  <Header.Subheader
                    style={{ marginTop: "0.5rem", color: "#666" }}
                  >
                    Приходите к нам в офис
                  </Header.Subheader>
                </Header.Content>
              </Header>

              <div
                style={{
                  background: "#f8f9fa",
                  padding: "1.5rem",
                  borderRadius: "8px",
                  marginTop: "1rem",
                  marginBottom: "1.5rem",
                }}
              >
                <p
                  style={{
                    fontSize: isMobile ? "1rem" : "1.1rem",
                    lineHeight: "2",
                  }}
                >
                  <Icon name="building" color="blue" />{" "}
                  <strong>ООО "Веб-разработка"</strong>
                  <br />
                  <Icon name="map pin" color="red" /> Москва, ул. Примерная, д.
                  123
                  <br />
                  <Icon name="phone" color="green" /> +7 (999) 123-45-67
                  <br />
                  <Icon name="mail" color="orange" /> info@webdev-example.ru
                </p>
              </div>

              {/* Карта (заглушка) */}
              <div
                style={{
                  height: "200px",
                  background: "#e9ecef",
                  borderRadius: "8px",
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center",
                  marginBottom: "1.5rem",
                }}
              >
                <p style={{ color: "#6c757d" }}>Карта местоположения</p>
              </div>

              <Header as="h2" style={{ color: "#4568dc", marginTop: "2rem" }}>
                <Icon name="users" />
                <Header.Content>
                  Мы в социальных сетях
                  <Header.Subheader
                    style={{ marginTop: "0.5rem", color: "#666" }}
                  >
                    Подписывайтесь на наши обновления
                  </Header.Subheader>
                </Header.Content>
              </Header>

              <div
                style={{
                  display: "flex",
                  flexWrap: "wrap",
                  gap: "10px",
                  marginTop: "1rem",
                  justifyContent: isMobile ? "center" : "flex-start",
                }}
              >
                {socialLinks.map((social, index) => (
                  <Button
                    key={index}
                    circular
                    size={isMobile ? "medium" : "large"}
                    icon={social.icon}
                    style={{
                      backgroundColor: social.color,
                      color: "white",
                      width: isMobile ? "45px" : "50px",
                      height: isMobile ? "45px" : "50px",
                    }}
                    as="a"
                    href={social.link}
                    target="_blank"
                    rel="noopener noreferrer"
                    title={social.name}
                  />
                ))}
              </div>
            </Segment>

            <Segment
              raised
              padded="very"
              style={{
                borderRadius: "10px",
                boxShadow: "0 2px 10px rgba(0,0,0,0.08)",
                marginTop: "1.5rem",
              }}
            >
              <Header as="h2" style={{ color: "#4568dc" }}>
                <Icon name="linkify" />
                <Header.Content>
                  Полезные ссылки
                  <Header.Subheader
                    style={{ marginTop: "0.5rem", color: "#666" }}
                  >
                    CSS-фреймворки и документация
                  </Header.Subheader>
                </Header.Content>
              </Header>

              <List divided relaxed selection style={{ marginTop: "1rem" }}>
                {cssFrameworks.map((framework, index) => (
                  <List.Item key={index} style={{ padding: "12px 8px" }}>
                    <List.Icon
                      name={framework.icon}
                      size="large"
                      verticalAlign="middle"
                      style={{ color: framework.color }}
                    />
                    <List.Content>
                      <List.Header style={{ marginBottom: "5px" }}>
                        {framework.name}
                      </List.Header>
                      <List.Description
                        style={{ display: "flex", gap: "15px" }}
                      >
                        <a
                          href={framework.docs}
                          target="_blank"
                          rel="noopener noreferrer"
                          style={{ color: "#4568dc" }}
                        >
                          <Icon name="file alternate" /> Документация
                        </a>
                        <a
                          href={framework.react}
                          target="_blank"
                          rel="noopener noreferrer"
                          style={{ color: "#4568dc" }}
                        >
                          <Icon name="react" /> React компоненты
                        </a>
                      </List.Description>
                    </List.Content>
                  </List.Item>
                ))}
              </List>
            </Segment>
          </Grid.Column>
        </Grid>

        <Segment
          textAlign="center"
          padded="very"
          style={{
            marginTop: "3rem",
            background: "linear-gradient(135deg, #f5f7fa, #e4e7eb)",
            borderRadius: "10px",
            border: "none",
          }}
        >
          <Header as="h2">Остались вопросы?</Header>
          <p style={{ fontSize: "1.1rem", marginBottom: "1.5rem" }}>
            Наша команда поддержки готова помочь вам в любое время
          </p>
          <Button
            size="large"
            primary
            style={{
              background: "linear-gradient(135deg, #4568dc, #b06ab3)",
              borderRadius: "30px",
              padding: "15px 30px",
              boxShadow: "0 4px 12px rgba(0,0,0,0.1)",
            }}
          >
            <Icon name="headphones" /> Связаться с поддержкой
          </Button>
        </Segment>

        <Divider hidden section />
      </Container>
    </PageLayout>
  );
};

export default ContactPage;
