import React, { useEffect, useState } from "react";
import {
  Container,
  Header,
  Button,
  Segment,
  Grid,
  Icon,
  Card,
  Image,
  Statistic,
  Label,
  Message
} from "semantic-ui-react";
import { Link } from "react-router-dom";
import { PageLayout } from "../../layout/PageLayout/PageLayout";

const frameworkLogos = {
  bootstrap: "https://www.pragimtech.com/wp-content/uploads/2019/04/bootstrap.jpg",
  tailwind: "https://i.ytimg.com/vi/Tq_7us1LlXA/maxresdefault.jpg",
  carbon: "https://i.pinimg.com/736x/93/ff/3a/93ff3a4b5711fdcebe25505e87ef6776.jpg",
  semantic: "https://i.ytimg.com/vi/aKuPuhccDQY/maxresdefault.jpg"
};

const HomePage = () => {
  const [windowWidth, setWindowWidth] = useState(window.innerWidth);
  const [isMobile, setIsMobile] = useState(window.innerWidth <= 768);

  // Обработчик изменения размера окна
  useEffect(() => {
    const handleResize = () => {
      setWindowWidth(window.innerWidth);
      setIsMobile(window.innerWidth <= 768);
    };

    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, [windowWidth]);

  // Адаптивные стили
  const getResponsiveStyles = () => {
    return {
      heroHeader: {
        fontSize: isMobile ? '2em' : '3em',
        fontWeight: 'bold',
        marginBottom: 0,
        marginTop: isMobile ? '1em' : '2em',
      },
      heroSubheader: {
        fontSize: isMobile ? '1.2em' : '1.5em',
        fontWeight: 'normal',
        marginTop: '0.5em',
        marginBottom: isMobile ? '1em' : '1.5em',
      },
      buttonGroup: {
        flexDirection: isMobile ? 'column' : 'row',
        display: 'flex',
        gap: isMobile ? '1em' : '0',
        width: isMobile ? '100%' : 'auto',
      },
      mobileButton: {
        width: isMobile ? '100%' : 'auto',
        margin: isMobile ? '0.5em 0' : '0',
      },
      sectionPadding: {
        padding: isMobile ? '2em 1em' : '4em 0em',
      },
      cardGroup: {
        justifyContent: 'center',
      },
      statisticsGrid: {
        gridTemplateColumns: isMobile ? '1fr 1fr' : '1fr 1fr 1fr 1fr',
        display: 'grid',
        gap: '1em',
      },
      statisticValue: {
        fontSize: isMobile ? '1.5rem' : '2rem',
      },
      statisticLabel: {
        fontSize: isMobile ? '0.9rem' : '1rem',
      },
      segmentCard: {
        margin: isMobile ? '1em 0' : '0',
      }
    };
  };

  const styles = getResponsiveStyles();

  return (
    <PageLayout>
      {/* Hero Section */}
      <Segment
        inverted
        textAlign="center"
        style={{ 
          minHeight: isMobile ? 350 : 500, 
          padding: isMobile ? '1em' : '2em 0em',
          backgroundImage: 'linear-gradient(135deg, #6a11cb 0%, #2575fc 100%)',
          marginTop: 0,
          borderRadius: 0
        }}
        vertical
      >
        <Container text>
          <Header
            as="h1"
            content="CSS Фреймворки"
            inverted
            style={styles.heroHeader}
          />
          <Header
            as="h2"
            content="Современные инструменты для создания стильных интерфейсов"
            inverted
            style={styles.heroSubheader}
          />
          <div style={styles.buttonGroup}>
            <Button 
              primary 
              as={Link} 
              to="/frameworks" 
              size={isMobile ? "large" : "huge"}
              style={styles.mobileButton}
            >
              <Icon name='info circle' />
              Обзор фреймворков
            </Button>
            <Button 
              secondary 
              as={Link} 
              to="/components" 
              size={isMobile ? "large" : "huge"}
              style={styles.mobileButton}
            >
              <Icon name='th' />
              Галерея компонентов
            </Button>
          </div>
        </Container>
      </Segment>

      {/* Статистика */}
      <Segment style={styles.sectionPadding} vertical>
        <Container>
          <div style={styles.statisticsGrid}>
            <div style={{ textAlign: 'center' }}>
              <Statistic size={isMobile ? 'small' : 'large'}>
                <Statistic.Value style={styles.statisticValue}>
                  <Icon name="code" color="blue" /> 4+
                </Statistic.Value>
                <Statistic.Label style={styles.statisticLabel}>Фреймворков</Statistic.Label>
              </Statistic>
            </div>
            <div style={{ textAlign: 'center' }}>
              <Statistic size={isMobile ? 'small' : 'large'}>
                <Statistic.Value style={styles.statisticValue}>
                  <Icon name="lightning" color="yellow" /> 50+
                </Statistic.Value>
                <Statistic.Label style={styles.statisticLabel}>Компонентов</Statistic.Label>
              </Statistic>
            </div>
            <div style={{ textAlign: 'center' }}>
              <Statistic size={isMobile ? 'small' : 'large'}>
                <Statistic.Value style={styles.statisticValue}>
                  <Icon name="users" color="green" /> 10M+
                </Statistic.Value>
                <Statistic.Label style={styles.statisticLabel}>Разработчиков</Statistic.Label>
              </Statistic>
            </div>
            <div style={{ textAlign: 'center' }}>
              <Statistic size={isMobile ? 'small' : 'large'}>
                <Statistic.Value style={styles.statisticValue}>
                  <Icon name="world" color="red" /> 85%
                </Statistic.Value>
                <Statistic.Label style={styles.statisticLabel}>Сайтов используют</Statistic.Label>
              </Statistic>
            </div>
          </div>
        </Container>
      </Segment>

      {/* Почему стоит использовать фреймворки */}
      <Segment style={styles.sectionPadding} vertical>
        <Container>
          <Header as="h2" textAlign="center" style={{ marginBottom: isMobile ? '1em' : '2em' }}>
            <Icon name="check circle" color="green" />
            Почему стоит использовать CSS-фреймворки?
          </Header>
          
          <Grid 
            columns={isMobile ? 1 : 3} 
            stackable 
            divided
            style={{ marginTop: isMobile ? '1em' : '2em' }}
          >
            <Grid.Row>
              <Grid.Column>
                <Segment raised padded style={styles.segmentCard}>
                  <Label as='a' color='blue' ribbon>
                    Эффективность
                  </Label>
                  <Header as="h3" icon textAlign="center">
                    <Icon name="rocket" circular />
                    <Header.Content>Быстрая разработка</Header.Content>
                  </Header>
                  <p style={{ fontSize: isMobile ? '1em' : '1.1em', textAlign: 'center' }}>
                    Сокращение времени разработки благодаря готовым компонентам и стилям, что позволяет сосредоточиться на функциональности.
                  </p>
                </Segment>
              </Grid.Column>
              <Grid.Column>
                <Segment raised padded style={styles.segmentCard}>
                  <Label as='a' color='teal' ribbon>
                    Удобство
                  </Label>
                  <Header as="h3" icon textAlign="center">
                    <Icon name="paint brush" circular />
                    <Header.Content>Гибкость дизайна</Header.Content>
                  </Header>
                  <p style={{ fontSize: isMobile ? '1em' : '1.1em', textAlign: 'center' }}>
                    Возможность легко настраивать и адаптировать компоненты под нужды проекта, сохраняя единый стиль.
                  </p>
                </Segment>
              </Grid.Column>
              <Grid.Column>
                <Segment raised padded style={styles.segmentCard}>
                  <Label as='a' color='orange' ribbon>
                    Надежность
                  </Label>
                  <Header as="h3" icon textAlign="center">
                    <Icon name="laptop" circular />
                    <Header.Content>Кросс-браузерность</Header.Content>
                  </Header>
                  <p style={{ fontSize: isMobile ? '1em' : '1.1em', textAlign: 'center' }}>
                    Гарантия совместимости со всеми популярными браузерами и устройствами без дополнительных усилий.
                  </p>
                </Segment>
              </Grid.Column>
            </Grid.Row>
          </Grid>
        </Container>
      </Segment>

      {/* Популярные фреймворки */}
      <Segment style={{ ...styles.sectionPadding, backgroundColor: '#f8f9fa' }} vertical>
        <Container>
          <Header as="h2" textAlign="center" style={{ marginBottom: isMobile ? '1em' : '2em' }}>
            <Icon name="star" color="yellow" />
            Популярные CSS-фреймворки
          </Header>
          
          <Card.Group 
            itemsPerRow={isMobile ? 1 : 4} 
            stackable
            style={styles.cardGroup}
          >
            <Card raised fluid={isMobile}>
              <Image src={frameworkLogos.bootstrap} wrapped ui={false} />
              <Card.Content>
                <Card.Header>Bootstrap</Card.Header>
                <Card.Meta>
                  <span className='date'>С 2011 года</span>
                </Card.Meta>
                <Card.Description>
                  Самый популярный CSS-фреймворк для создания адаптивных и мобильных веб-сайтов.
                </Card.Description>
              </Card.Content>
              <Card.Content extra>
                <div style={{ display: 'flex', flexWrap: 'wrap', gap: '0.5em' }}>
                  <Label color='purple' tag>HTML</Label>
                  <Label color='purple' tag>CSS</Label>
                  <Label color='purple' tag>JS</Label>
                </div>
              </Card.Content>
            </Card>

            <Card raised fluid={isMobile}>
              <Image src={frameworkLogos.tailwind} wrapped ui={false} />
              <Card.Content>
                <Card.Header>Tailwind CSS</Card.Header>
                <Card.Meta>
                  <span className='date'>С 2017 года</span>
                </Card.Meta>
                <Card.Description>
                  Утилитарный CSS-фреймворк для быстрого создания пользовательских интерфейсов.
                </Card.Description>
              </Card.Content>
              <Card.Content extra>
                <div style={{ display: 'flex', flexWrap: 'wrap', gap: '0.5em' }}>
                  <Label color='blue' tag>Utility-First</Label>
                  <Label color='blue' tag>Гибкость</Label>
                </div>
              </Card.Content>
            </Card>

            <Card raised fluid={isMobile}>
              <Image src={frameworkLogos.carbon} wrapped ui={false} />
              <Card.Content>
                <Card.Header>Carbon Design</Card.Header>
                <Card.Meta>
                  <span className='date'>С 2018 года</span>
                </Card.Meta>
                <Card.Description>
                  Система дизайна от IBM для создания последовательных и понятных пользовательских интерфейсов.
                </Card.Description>
              </Card.Content>
              <Card.Content extra>
                <div style={{ display: 'flex', flexWrap: 'wrap', gap: '0.5em' }}>
                  <Label color='black' tag>Enterprise</Label>
                  <Label color='black' tag>Accessibility</Label>
                </div>
              </Card.Content>
            </Card>

            <Card raised fluid={isMobile}>
              <Image src={frameworkLogos.semantic} wrapped ui={false} />
              <Card.Content>
                <Card.Header>Semantic UI</Card.Header>
                <Card.Meta>
                  <span className='date'>С 2014 года</span>
                </Card.Meta>
                <Card.Description>
                  Фреймворк с человекопонятным синтаксисом и богатым набором компонентов.
                </Card.Description>
              </Card.Content>
              <Card.Content extra>
                <div style={{ display: 'flex', flexWrap: 'wrap', gap: '0.5em' }}>
                  <Label color='teal' tag>Интуитивный</Label>
                  <Label color='teal' tag>Богатый API</Label>
                </div>
              </Card.Content>
            </Card>
          </Card.Group>
        </Container>
      </Segment>

      {/* Советы */}
      <Segment style={styles.sectionPadding} vertical>
        <Container>
          <Message
            icon={isMobile ? null : "idea"}
            header="Совет дня"
            content="Сравнивайте фреймворки не только по популярности, но и по подходящести для вашего проекта. Разные задачи могут требовать разных инструментов."
            info
            size={isMobile ? 'small' : 'large'}
          />
          {isMobile && (
            <p style={{ textAlign: 'center', marginTop: '1em' }}>
              <Icon name="mobile alternate" /> Вся информация доступна на мобильных устройствах
            </p>
          )}
        </Container>
      </Segment>

      {/* CTA */}
      <Segment inverted vertical style={{ padding: '3em 0em', backgroundColor: '#2c3e50' }}>
        <Container text textAlign='center'>
          <Header inverted as='h2'>
            Готовы изучить подробнее?
          </Header>
          <p style={{ fontSize: '1.2em', marginBottom: '2em' }}>
            Ознакомьтесь с нашими материалами по каждому фреймворку и начните использовать их в своих проектах уже сегодня.
          </p>
          <Button.Group size="large">
            <Button primary as={Link} to="/frameworks" size="huge">
              Начать обзор
            </Button>
            <Button.Or text="или" />
            <Button secondary as={Link} to="/comparison" size="huge">
              Сравнение
            </Button>
          </Button.Group>
        </Container>
      </Segment>
    </PageLayout>
  );
};

export default HomePage;

